import axios, {
  InternalAxiosRequestConfig,
  AxiosResponse,
  AxiosError,
} from 'axios';
import { getToken, clearAuthData } from './auth';
import { getApiUrl } from './apiConfig';

// vite-env.d.ts   
const isDevelopment = import.meta.env.MODE === 'development';

let logoutHandler: (() => void) | null = null;
export const setLogoutHandler = (handler: () => void) => {
  logoutHandler = handler;
  if (isDevelopment) {
    console.log('   .');
  }
};

const publicApiPaths = ['/auth/login', '/auth/register', '/auth/health'];

interface ErrorResponse {
  message: string;
  status?: number;
  code?: string;
}

// YouTube API   axios  
export const youtubeApi = axios.create({
  baseURL: 'https://www.googleapis.com/youtube/v3',
  timeout: 10000,
});

//     axios 
export const configApi = axios.create({
  timeout: 5000,
});

//  baseURL  API   
export const createApi = () => {
  // API  URL  (apiConfig )
  const baseURL = getApiUrl();
  if (isDevelopment) {
    console.log(' API  URL:', baseURL);
  }

  const apiInstance = axios.create({
    baseURL,
    timeout: 10000,
    headers: {
      'Content-Type': 'application/json',
    },
    // CORS  
    withCredentials: true,
  });

  //  
  apiInstance.interceptors.request.use(
    (config: InternalAxiosRequestConfig) => {
      const token = getToken();
      const isPublicApi = publicApiPaths.some(path => config.url?.includes(path));

      if (token && !isPublicApi) {
        config.headers.Authorization = `Bearer ${token}`;
        if (isDevelopment) {
          console.log('   :', token.substring(0, 20) + '...');
        }
      } else if (!isPublicApi && isDevelopment) {
        console.warn('     :', config.url);
      }

      if (isDevelopment) {
        console.log(' :', {
          url: config.url,
          method: config.method,
          headers: config.headers,
          data: config.data,
          isPublicApi,
        });
      }
      return config;
    },
    (error: AxiosError) => {
      console.error('  :', error);
      return Promise.reject(error);
    }
  );

  //  
  apiInstance.interceptors.response.use(
    (response: AxiosResponse) => {
      if (isDevelopment) {
        console.log(' :', {
          status: response.status,
          data: response.data,
        });
      }
      return response;
    },
    async (error: unknown) => {
      //  AxiosError  
      const axiosError = error as AxiosError<ErrorResponse>;
      
      if (isDevelopment) {
        console.error('  :', {
          status: axiosError.response?.status,
          data: axiosError.response?.data,
          message: axiosError.message,
          code: axiosError.code,
        });
      }

      if (axios.isCancel(axiosError)) {
        console.log('  .');
        return Promise.reject(new Error(' .'));
      }

      if (axiosError.response?.status === 401) {
        console.warn(' 401 Unauthorized -   ');
        clearAuthData();
        if (logoutHandler) {
          logoutHandler();
        } else {
          window.location.href = '/login';
        }
        return Promise.reject(new Error(' .'));
      }

      if (axiosError.code === 'ECONNABORTED') {
        console.error('   ');
        return Promise.reject(new Error('  .'));
      }

      if (!axiosError.response) {
        console.error('  :', axiosError.message);
        return Promise.reject(new Error('   .'));
      }

      return Promise.reject(axiosError);
    }
  );

  return apiInstance;
};

//  API  
const api = createApi();

export default api;
