import axios from 'axios';

interface ApiConfig {
  apiUrl: string;
  wsUrl: string;
  version?: string;
  env?: string;
}

//  API 
let apiConfig: ApiConfig = {
  apiUrl: import.meta.env.VITE_API_URL || 'http://localhost:3001',
  wsUrl: import.meta.env.VITE_WS_URL || 'ws://localhost:3001',
};

// API    
const saveApiConfig = (config: ApiConfig) => {
  localStorage.setItem('api_config', JSON.stringify(config));
};

// API    
const loadApiConfig = (): ApiConfig | null => {
  const saved = localStorage.getItem('api_config');
  return saved ? JSON.parse(saved) : null;
};

// API   (   )
export const initApiConfig = async (): Promise<ApiConfig> => {
  //   API  
  if (import.meta.env.DEV) {
    try {
      //     
      const savedConfig = loadApiConfig();
      if (savedConfig) {
        apiConfig = savedConfig;
        console.log('  API  :', apiConfig);
      }

      //     
      console.log('   API   ...');
      const response = await axios.get<ApiConfig>('/api/config');
      
      if (response.data?.apiUrl) {
        apiConfig = response.data;
        console.log('  API  :', apiConfig);
        
        //   
        saveApiConfig(apiConfig);
      }
    } catch (error) {
      console.error(' API    :', error);
      console.warn('  API  :', apiConfig);
    }
  }
  
  return apiConfig;
};

//  API URL 
export const getApiUrl = (): string => {
  return apiConfig.apiUrl;
};

//  WebSocket URL 
export const getWsUrl = (): string => {
  return apiConfig.wsUrl;
};

export default {
  initApiConfig,
  getApiUrl,
  getWsUrl
}; 