import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { RootState } from '../app/store';

const Home = () => {
  const navigate = useNavigate();
  const { isAuthenticated } = useSelector((state: RootState) => state.auth);

  return (
    <div className="max-w-4xl mx-auto p-6">
      <div className="text-center">
        <h1 className="text-4xl font-bold text-gray-800 mb-6">Love Tree   </h1>
        <p className="text-xl text-gray-600 mb-8">
             
        </p>
        
        <div className="space-y-4">
          {isAuthenticated ? (
            <div className="space-y-4">
              <button
                onClick={() => navigate('/trees')}
                className="px-8 py-3 bg-pink-500 text-white rounded-lg hover:bg-pink-600 transition-colors"
              >
                   
              </button>
              <div>
                <button
                  onClick={() => navigate('/trees/create')}
                  className="px-8 py-3 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
                >
                    
                </button>
              </div>
            </div>
          ) : (
            <div className="space-y-4">
              <button
                onClick={() => navigate('/login')}
                className="px-8 py-3 bg-pink-500 text-white rounded-lg hover:bg-pink-600 transition-colors"
              >
                
              </button>
              <div>
                <button
                  onClick={() => navigate('/register')}
                  className="px-8 py-3 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
                >
                  
                </button>
              </div>
            </div>
          )}
        </div>

        <div className="mt-12 bg-gray-50 p-8 rounded-lg">
          <h2 className="text-2xl font-semibold text-gray-800 mb-4">Love Tree </h2>
          <div className="text-gray-600 space-y-4">
            <p>
              Love Tree        .
            </p>
            <p>
                   .
            </p>
            <p>
                      .
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Home; 